<template>
  <div class="users">
      <div class="imgbig">
        <img src="/static/bid.png" alt="">
      </div>
      <div class="shop">
      <h1>耐克产品 (2433)</h1>
      <ul>
        <li v-for="user in users">
          <!--<h2>{{user.name}}</h2>-->
          <!--<h3 >{{user.email}}</h3>-->
          <a href="">
            <div ><img v-lazy=user.nameurl alt="" >

            </div>
            <p class="bottomline">三色</p>
            <p>经典推荐</p>
            <p class="color">女子运动鞋</p>
            <p class="color">￥999</p>
