          // {name: "herry", position: "web 开发", show: false},
          // {name: "herry", position: "web 开发", show: false},
          // {name: "herry", position: "web 开发", show: false},
          // {name: "herry", position: "web 开发", show: false},
          // {nameurl:'/static/image/1.jpg'},
          // {nameurl:'/static/image/2.jpg'},
          // {nameurl:'/static/image/3.jpg'},
          // {nameurl:'/static/image/4.jpg'},
          // {nameurl:'/static/image/5.jpg'},
          // {nameurl:'/static/image/6.jpg'},
          // {nameurl:'/static/image/7.jpg'},
          // {nameurl:'/static/image/8.jpg'},
          // {nameurl:'/static/image/9.jpg'},
          // {nameurl:'/static/image/10.jpg'},
          // {nameurl:'/static/image/11.jpg'},
          // {nameurl:'/static/image/12.jpg'},
          // {nameurl:'/static/image/13.jpg'},
          // {nameurl:'/static/image/14.jpg'},
          // {nameurl:'/static/image/15.jpg'},
          // {nameurl:'/static/image/16.jpg'},
          // {nameurl:'/static/image/17.jpg'},
        ]
      }
    },
    created() {
      axios.get('https://kuangkuang520.github.io/data/1.json').then(response=>{
        // console.log(response.data);
        this.users=response.data;
      })
