<!--CSS :作用域scoped-->
<style scoped>
.users{
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
}
.shop{
  padding: 0;
}
.imgbig{
padding: 0 20px;
}
  ul{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style-type: none;
    padding: 0;
  }

  li{
    flex-grow: 0;
    flex-basis: 260px;
    /* flex-basis使用固定值是相对于max-width的基准值 */
    text-align: center;
    padding: 20px;
    box-sizing: border-box;
  }
  li>a>div{
    width:100%;
    height: 0;
    padding-bottom: 100%;
    overflow:hidden;
  }
img{
  width:100%;

}
li:hover{
  border: 1px solid lightgray;
  z-index: 5;
  }
  a{
    color: black;
    text-decoration:none;
    font-size: 0.8em;
  }
  a>p{
    display: block;
    width: 100%;
    margin: 0;
    padding: 10px 0;
    text-align: left;
  }
  .bottomline{
    border-bottom: 1px solid lightgrey;
  }
  .color{
    color: gray;
    padding: 0;
  }
</style>



