<template>
  <footer>
    <p>{{copyright}}</p>
  </footer>
</template>

<script>
    export default {
      name:'app-footer',
      data(){
        return {
          copyright: "copyright 2019 Vue demo"
        }
      }

    }
</script>

<style scoped>
  footer{
    background: black;
    padding: 6px;
  }
  p{
    text-align: center;
    color:white;
  }
</style>



